<template>
  <div>
    <section class="head">
      <div><a @click="goBack" class="iconfont back"> &#xe614; </a></div>
      <h1>Become Seller</h1>
      <div class="chartWrap"></div>
    </section>
    <div class="">
      <van-cell-group>
        <div class="titleWrap">
          <span>Upgrade to Shopeone Seller Acount</span>
        </div>
        <van-field v-model="form.firstName" placeholder="First Name" />
        <van-field v-model="form.lastName" placeholder="Last Name" />
        <van-field v-model="form.email" placeholder="Email" />
        <div class="genderWrap"  @click="control = true">
          <van-field
            v-model="genderName"
            disabled
            right-icon="arrow-down"
            placeholder="Gender"
          />
          <div class="witch" v-show="control">
            <!-- <p>Men</p>
                  <p>Women</p> -->
            <p
              v-for="(item, index) in genderList"
              class="hover"
              @click.stop="genderSel(item.name,index)"
              :key="index"
            >
              {{ item.name }}
            </p>
          </div>
        </div>
      </van-cell-group>
      <van-cell-group>
        <div class="titleWrap">
          <span>Your store Name</span>
        </div>
        <van-field
          v-model="form.storeName"
          placeholder="The same as your store's brand"
        />
      </van-cell-group>
      <van-cell-group>
        <div class="titleWrap">
          <span>Your store Location</span>
        </div>
        <van-field
          v-model="form.countryName"
          @click="showPicker(0)"
           :readonly="true"
          right-icon="arrow-down"
          placeholder="Country"
        />
        <van-field
          v-model="form.cityName"
          right-icon="arrow-down"
           :readonly="true"
          placeholder="City"
           @click="showPicker(1)"
        />
        <van-field
          v-model="form.regionName"
          right-icon="arrow-down"
           :readonly="true"
          placeholder="Region"
           @click="showPicker(2)"
        />
        <van-field v-model="form.street" placeholder="Street" />
        <van-field v-model="form.landmark" placeholder="Nearest Landmark" />
      </van-cell-group>
    </div>
    <div class="btmWrap">
      <van-field v-model="form.mobile" placeholder="Mobile Number" />
      <van-field v-model="form.landlineNumber" placeholder="Landline Number" />
      <van-button type="danger" @click="submitStore">Submit</van-button>
      <span class="tips"
        >Our customer service staff will contact you within 3 days. We're
        looking forward to cooperating with you.</span
      >
    </div>

   <div class="pickerWrap" v-show="showPick" @click="showPick = false"></div>
    <div class="picker">
      <van-picker
        :columns="pickList"
        show-toolbar
        ref="picker"
        v-show="showPick"
        @cancel="onCancel"
        @confirm="onConfirm"
      />
    </div>
  </div>
</template>

<script>
import { country, city } from "@/api/address";
import { addStore } from "@/api/store";
export default {
  name: "shopeone",
  data() {
    return {
      countryList: [],
      cityList: [],
      pickList: [],
      regionList: [],
      address: {},
      level: 0,
      genderName:"",
      showPick: false,
      form: {
        firstName: "",
        lastName: "",
        email: "",
        gender: "",
        storeName: "",
        countryCode: "",
        cityCode: "",
        regionCode: "",
        countryName: "",
        cityName: "",
        regionName: "",
        street: "",
        building: null,
        mobile: "",
        landlineNumber: "",
        longitude: "",
        latitude: "",
        img: null,
        remark: null
      },
      genderList: [{ name: "Men" }, { name: "Women" }],
      control: false
    };
  },
  computed: {},
  mounted() {
    this.init();
  },
  methods: {
    init() {
      this.$store.dispatch("getUserInfo").then(res => {
        this.form.longitude=res.longitude;
        this.form.latitude=res.latitude;
      });

      country().then(res => {
        this.countryList = res.data.result;
        this.form.countryCode = this.countryList[0].countryCode;
        this.form.countryName = this.countryList[0].countryName;
        city({ countryCode: this.form.countryCode, parentCode: 0 }).then(
          res => {
            this.cityList = res.data.result;
          }
        );
      });
    },
    changeSort(val) {
      this.sortValue = val;
      this.sortVisible = false;
    },
    goBack() {
      this.$router.back(-1);
    },
    submitStore() {
      addStore(this.form).then(res => {
        this.$toast("Submit successfully. Please wait for us to verify");
      });
    },
    genderSel(genderName,index) {
      this.form.gender = index+1;
      this.genderName = genderName;
      this.control = false;
    },
    showPicker(level) {
      this.level = level;
      switch (level) {
        case 0:
          this.pickList = this.countryList.map(c => {
            return c.countryName;
          });
          break;
        case 1:
          this.pickList = this.cityList.map(c => {
            return c.regionName;
          });
          break;
        case 2:
          this.pickList = this.regionList.map(c => {
            return c.regionName;
          });
          break;
      }

      this.showPick = true;
    },
    onCancel() {
      this.showPick = false;
    },
    onConfirm(name, index) {
      this.showPick = false;
      switch (this.level) {
        case 0:
          this.form.countryCode = this.countryList[0].countryCode;
          this.form.countryName = this.countryList[0].countryName;
          break;
        case 1:
          this.form.cityCode = this.cityList[index].regionCode;
          this.form.cityName = name;
          city({
            countryCode: this.form.countryCode,
            parentCode: this.form.cityCode
          }).then(res => {
            this.regionList = res.data.result;
          });
          break;
        case 2:
          this.form.regionCode = this.regionList[index].regionCode;
          this.form.regionName = name;
          break;
      }
    },
    confirm() {
      // this.$router.push({path:'veryMobile',query:{type:"",extra:this.form}})
      let func = this.form.id ? editAddress : addAddress;
      this.form.addresType = this.checked ? 1 : 0;
      func(this.form).then(res => {
        this.$toast("Save address successfully");
        this.$router.push('/');
      });
    }
  }
};
</script>

<style lang="scss" scoped>
.titleWrap {
  padding: 10px 0;
  border-bottom: solid 1px #f4f4f4;
  span {
    padding: 0 15px;
    border-left: 3px solid red;
    font-size: 15px;
    color: #333333;
  }
}
.van-cell-group {
  border-top: 10px solid #f6f6f6;
}
.btmWrap {
  background-color: #f6f6f6;
  .van-button--danger {
    width: 92%;
    margin-left: 4%;
    margin-top: 60px;
  }
  .van-cell {
    background-color: #f6f6f6;
  }
  .tips {
    display: block;
    padding: 10px 6% 20px;
    color: #ba1b21;
    font-size: 12px;
  }
}
.van-cell {
  color: #999999;
  font-size: 14px;
}

.genderWrap {
  position: relative;
}
.genderWrap .witch {
  position: absolute;
  right: 15px;
  background: #fff;
  border-radius: 5px;
  box-shadow: 0 0 5px 2px rgba(23, 23, 23, 0.2);
  padding: 5px 5px 10px;
  z-index: 1000;
  height: 90px;
}
.genderWrap .witch p {
  padding: 10px 10px;
  text-align: center;
  white-space: normal;
  min-width: 120px;
  font-size: 14px;
}
.picker {
  position: fixed;
  bottom: 0;
  width: 100%;
  z-index: 120;
}
.pickerWrap {
  width: 100%;
  height: 100%;
  opacity: 0.5;
  background-color: rgba(0, 0, 0, 0.7);
  z-index: 100;
  position: fixed;
  top: 0;
}
</style>



